<template>
<section>
  <!--工具条-->
  <el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
    <el-form :inline="true">
      <el-form-item>
        <span> <i class="el-icon-location l-style"></i>全部产品</span>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="handleAdd">+ 添加产品</el-button>
      </el-form-item>
      <el-form-item style="float: right;">
        产品共：<span v-text="total"></span>个
      </el-form-item>
    </el-form>
  </el-col>

  <!--列表-->
  <el-table :data="product_id"
    highlight-current-row
    @selection-change="selsChange" style="width: 100%;"  height="500">
    <el-table-column type="selection" width="50"></el-table-column>
    <el-table-column label="编号" width="50">
      <template slot-scope="scope">
        <span v-text="Number(scope.$index) + 1 + (page - 1) * 10"></span>
      </template>
    </el-table-column>
    <el-table-column prop="image" label="缩略图" width="70">
      <template slot-scope="scope">
        <img :src="scope.row.image" height="40" width="40" />
      </template>
    </el-table-column>
    <el-table-column prop="name" label="产品名称" min-width="80"></el-table-column>
    <el-table-column label="产品类型" min-width="120">
      <template slot-scope="scope">
        <span v-text="JSON.parse(scope.row.categorys).join('  ')"></span>
      </template>
    </el-table-column>
    <el-table-column label="排序操作" width="130" fixed="right">
      <template slot-scope="scope">
        <el-button size="medium" type='text' @click.stop="sorttop(scope.$index, scope.row)">至顶 </el-button>
        <el-button size="medium" type='text' @click.stop="sortUp(scope.$index, scope.row)">↑ </el-button>
        <el-button size="medium" type='text' @click.stop="sortDown(scope.$index, scope.row)">↓</el-button>
      </template>
    </el-table-column>
    <el-table-column label="操作" width="150" fixed="right">
      <template slot-scope="scope">
        <el-button size="small" @click="handleEdit(scope.$index, scope.row)">编辑</el-button>
        <el-button type="danger" size="small" @click="handleDel(scope.$index, scope.row)">删除</el-button>
      </template>
    </el-table-column>
  </el-table>

  <!--工具条-->
  <el-col :span="24" class="toolbar">
    <el-button type="danger" @click="batchRemove" :disabled="this.sels.length===0">批量删除</el-button>
    <el-pagination layout="prev, pager, next" @current-change="handleCurrentChange" :page-size="10" :total="total" style="float:right;">
    </el-pagination>
  </el-col>

  <!--新增界面-->
  <el-dialog  max-height="400" title="填写商品信息" v-model="addFormVisible" @close="cancel()">
    <el-form :model="addForm" label-width="100px" :rules="addFormRules" ref="addForm" style="max-height: 50vh; overflow-y: auto; padding:0px 30px 0 0;" >
  <el-form-item label="产品图片">
        <el-row style="margin-bottom: 20px;">
          <el-col :span="5">
            <el-upload class="avatar-uploader" :file-list="image_0" list-type="picture" action="http://94.191.23.153:8888/api/image/upload" :on-remove="handleRemove1" :on-success="handleAvatarSuccess1" :before-upload="beforeAvatarUpload">
              <i class="el-icon-plus avatar-uploader-icon"></i>
            </el-upload>
          </el-col>
          <el-col :span="5" :offset="1">
            <el-upload class="avatar-uploader" list-type="picture" action="http://94.191.23.153:8888/api/image/upload" :on-remove="handleRemove2" :on-success="handleAvatarSuccess2" :file-list="image_1" :before-upload="beforeAvatarUpload">
              <i class="el-icon-plus avatar-uploader-icon"></i>
            </el-upload>
          </el-col>
          <el-col :span="5" :offset="1">
            <el-upload class="avatar-uploader" action="http://94.191.23.153:8888/api/image/upload" :on-remove="handleRemove3" :on-success="handleAvatarSuccess3" :file-list="image_2" :before-upload="beforeAvatarUpload" list-type="picture">
              <i class="el-icon-plus avatar-uploader-icon"></i>
            </el-upload>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="5">
            <el-upload class="avatar-uploader" action="http://94.191.23.153:8888/api/image/upload" :on-remove="handleRemove4" :on-success="handleAvatarSuccess4" :file-list="image_3" :before-upload="beforeAvatarUpload" list-type="picture">
              <i class="el-icon-plus avatar-uploader-icon"></i>
            </el-upload>
          </el-col>

          <el-col :span="5" :offset="1">
            <el-upload class="avatar-uploader" action="http://94.191.23.153:8888/api/image/upload" :on-remove="handleRemove5" :on-success="handleAvatarSuccess5" :file-list="image_4" :before-upload="beforeAvatarUpload" list-type="picture">
              <i class="el-icon-plus avatar-uploader-icon"></i>
            </el-upload>
          </el-col>
        </el-row>
      </el-form-item>

      <div class="pictext">上传尺寸776*776的JPG/JPEG/PNG/GIF格式</div>
      <el-form-item prop="name" label="产品名称">
        <el-input v-model="addForm.name" auto-complete="off" :maxlength="10" placeholder="请输入产品名称（10字以内）"></el-input>
      </el-form-item>
      <el-form-item prop="category" label="产品类别">
        <el-checkbox-group v-model="addForm.category_id">
          <el-checkbox v-for="(item, a) in categoryList" :label="String(item.category_id)" :key="a">{{item.name}}</el-checkbox>
        </el-checkbox-group>
      </el-form-item>
      <el-row>
        <el-col :span="5">
          <el-form-item prop="group_price" label="拼团价格">
            <el-input @blur="computerRate" v-model.number="addForm.group_price" auto-complete="off" :maxlength="7" placeholder="数字"></el-input>
          </el-form-item>
        </el-col>
        <el-col :span="5">
          <el-form-item prop="market_price" label="市场售价">
            <el-input @blur="computerRate" v-model.number="addForm.market_price" auto-complete="off" :maxlength="7" placeholder="数字"></el-input>
          </el-form-item>
        </el-col>
        <el-col :span="5">
          <el-form-item label="优惠比例">
            <el-input v-model="addForm.rate" type="context" auto-complete="off" :maxlength="8" disabled> </el-input>
          </el-form-item>
        </el-col>
        <el-col :span="4">
          <el-form-item prop="charge_unit" label="计价单位">
            <el-input v-model="addForm.charge_unit" type="context" auto-complete="off" :maxlength="8" placeholder="¥等"> </el-input>
          </el-form-item>
        </el-col>
        <el-col :span="4">
          <el-form-item prop="group_number" label="成团人数">
            <el-input v-model.number="addForm.group_number" auto-complete="off" :maxlength="7" placeholder="3"> </el-input>
          </el-form-item>
        </el-col>
      </el-row>
      <el-form-item prop="community_id" label="所属社区">
        <el-checkbox-group v-model="addForm.community_id">
          <el-checkbox v-for="(item, a) in communityList" :label="String(item.community_id)" :key="a">{{item.name}}</el-checkbox>
        </el-checkbox-group>
      </el-form-item>
      <el-form-item prop="brief" label="产品简介">
        <el-input type="textarea" v-model="addForm.brief" :maxlength="35" :rows="1" resize="none" placeholder="请输入产品简介（35字以内）"> </el-input>
      </el-form-item>
      <el-form-item prop="sell_point" label="核心卖点">
        <el-input type="textarea" v-model="addForm.sell_point" :maxlength="20" :rows="1" resize="none" placeholder="请输入核心卖点（20字以内）"> </el-input>
      </el-form-item>
      <br />
      <!--使用编辑器 :options="editorOption" @ready="onEditorReady($event)"-->
      <el-form-item label="产品详情" prop="detail" class="edit_container edit-style">
        <quill-editor ref="myQuillEditor" v-model="addForm.detail" class="editer">
        </quill-editor>
      </el-form-item>
      <br />
      <el-form-item label="品牌介绍" prop="introduction" class="edit_container edit-style">
        <quill-editor ref="myQuillEditor" v-model="addForm.introduction" class="editer">
        </quill-editor>
      </el-form-item>
      <br />
      <el-form-item label="运输及售后" class="edit_container edit-style" prop="transport_sale">
        <quill-editor ref="myQuillEditor" v-model="addForm.transport_sale" class="editer">
        </quill-editor>
      </el-form-item>
    </el-form>
    <div slot="footer" class="dialog-footer">
      <el-button @click.native="cancel()">取消</el-button>
      <el-button type="primary" @click.native="addSubmit" :loading="addLoading">提交</el-button>
    </div>
  </el-dialog>
</section>
</template>

<script>
import Vue from 'vue'
import util from '../../common/js/util'
import {
  quillEditor
} from 'vue-quill-editor' //调用编辑器
import 'quill/dist/quill.core.css'
import 'quill/dist/quill.snow.css'
import {
  getProductsListPage,
  removeProducts,
  addProducts,
  getProtypeList,
  getSitesList,
  deleteImage,
  isToTop,
  isToUp
} from '../../api/api';
export default {
  data() {
    return {
      categoryList: [],
      total: 0,
      product_id: [],
      communityList: [],
      image_0: [],
      image_1: [],
      image_2: [],
      image_3: [],
      image_4: [],
      imageUrl: '',
      page: 1,
      listLoading: false,
      sels: [], //列表选中列
      addFormVisible: false, //新增界面是否显示
      addLoading: false,
      addFormRules: {
        name: [{
          required: true,
          message: '请输入商品名称（10字以内）',
          trigger: 'blur'
        }],
        category_id: [{
          type: 'array',
          required: true,
          message: '请选择产品类别',
          trigger: 'blur'
        }],
        community_id: [{
          type: 'array',
          required: true,
          message: '请选择所属社区',
          trigger: 'blur'
        }],
        brief: [{
          required: true,
          message: '请填写产品简介（35字以内）',
          trigger: 'blur'
        }],
        group_price: [{
          type: 'number',
          message: '拼团价必须为数值'
        }],
        market_price: [{
          type: 'number',
          message: '市场价必须为数值'
        }],
        charge_unit: [{
          required: true,
          message: '请输入计价单位',
          trigger: 'blur'
        }],
        group_number: [{
          type: 'number',
          message: '成团人数必须为数值'
        }],
        sell_point: [{
          required: true,
          message: '请输入核心卖点（20字以内）',
          trigger: 'blur'
        }],
        detail: [{
          required: true,
          message: '请输入产品详情',
          trigger: 'blur'
        }],
        introduction: [{
          required: true,
          message: '请输入品牌介绍',
          trigger: 'blur'
        }],
        transport_sale: [{
          required: true,
          message: '请输入运输及售后',
          trigger: 'blur'
        }]
      },
      //新增界面数据
      addForm: {
        name: '',
        community_id: [],
        brief: '',
        category_id: [],
        group_price: 0,
        rate: '',
        market_price: 0,
        charge_unit: '',
        group_number: 0,
        sell_point: '',
        detail: '',
        introduction: '',
        transport_sale: '',
        image_0: '',
        image_1: '',
        image_2: '',
        image_3: '',
        image_4: ''
      }
    }
  },
  methods: {
    // 上移按钮
    sortUp(index, row) {
      if (index === 0) {
        this.$message({
          message: '已经是列表中第一个素材！',
          type: 'warning'
        })
      } else {
        isToUp ({
          above_product_id: this.product_id[index - 1].product_id,
          under_product_id: this.product_id[index].product_id
        }).then((res) => {
          if (res.data.code === 0) {
            this.getProducts()
          }
        })
      }
    },
    // 至顶按钮
    sorttop(index, row) {
      if (index === 0) {
        this.$message({
          message: '已经是列表中第一个素材！',
          type: 'warning'
        })
      } else {
        isToTop ({
          product_id: row.product_id,
          is_top: 1
        }).then((res) => {
          if (res.data.code === 0) {
            this.getProducts()
          }
        })
      }
    },
    // 下移按钮
    sortDown(index, row) {
      if (index === (this.product_id.length - 1)) {
        this.$message({
          message: '已经是列表中最后一个素材！',
          type: 'warning'
        })
      } else {
        isToUp ({
          above_product_id: this.product_id[index].product_id,
          under_product_id: this.product_id[index + 1].product_id
        }).then((res) => {
          if (res.data.code === 0) {
            this.getProducts()
          }
        })
      }
    },
    //pageNum变化事件
    handleCurrentChange(val) {
      this.page = val
      this.getProducts();
    },
    //获取产品列表
    getProducts() {
      let para = {
        page: this.page,
        size: 10
      };
      getProductsListPage(para).then((res) => {
        this.total = res.data.total;
        this.product_id = res.data.data;
        if (this.total !== 0) {
          this.product_id.map((item) => {
            item.image = 'http://94.191.23.153/images/' + item.image_0
          })
        }
      });
    },
    initProductCatagory() {
      getProtypeList().then((res) => {
        if (res.data.total !== 0) {
          this.categoryList = res.data.data
        }
      })
    },
    initCommunityList() {
      getSitesList().then((res) => {
        if (res.data.total !== 0) {
          this.communityList = res.data.data
        }
      })
    },
    //显示编辑界面
    handleEdit: function(index, row) {
      this.addFormVisible = true;
      this.initProductCatagory()
      this.initCommunityList()
      getProductsListPage({
        product_id: row.product_id
      }).then((res) => {
          if (res.data.code === 0) {
            this.image_0.push({
              url: 'http://94.191.23.153/images/' + res.data.data.image_0
            })
            this.image_1.push({
              url: 'http://94.191.23.153/images/' + res.data.data.image_1
            })
            this.image_2.push({
              url: 'http://94.191.23.153/images/' + res.data.data.image_2
            })
            this.image_3.push({
              url: 'http://94.191.23.153/images/' + res.data.data.image_3
            })
            this.image_4.push({
              url: 'http://94.191.23.153/images/' + res.data.data.image_4
            })
            this.addForm = Object.assign({}, res.data.data);
          }
      })
    },
    //显示新增界面
    handleAdd: function() {
      this.addFormVisible = true;
      this.initProductCatagory()
      this.initCommunityList()
    },
    cancel() {
      this.addFormVisible = false;
      this.resetForm();
    },
    beforeAvatarUpload(files) {
      const isJPG = files.type === 'image/jpg';
      const isJPEG = files.type === 'image/jpeg';
      const isPNG = files.type === 'image/png';
      const isGIF = files.type === 'image/gif';
      const isLt2M = files.size / 1024 / 1024 < 2;

      if (!isJPG && !isJPEG && !isPNG && !isGIF && !isGIF) {
        this.$message.error('上传图片格式只能是 JPG/JPEG/PNG/GIF 格式!');
      }
      if (!isLt2M) {
        this.$message.error('上传图片大小不能超过 2MB!');
      }
      return isJPG || isJPEG || isPNG || isGIF && isLt2M;
    },
    handleRemove1(fileList) {
      this.addForm.image_0 = ''
      deleteImage({
        image_name: fileList.url.split('/')[fileList.url.split('/').length - 1]
      }).then((res) => {
        this.$message({
          message: '删除成功',
          type: 'success'
        });
      })
    },
    handleAvatarSuccess1(file, fileList) {
      this.addForm.image_0 = file.data.image_name[0]
      this.imageUrl = URL.createObjectURL(fileList.raw);
    },
    handleRemove2(fileList) {
      this.addForm.image_1 = ''
      deleteImage({
        image_name: fileList.url.split('/')[fileList.url.split('/').length - 1]
      }).then((res) => {
        this.$message({
          message: '删除成功',
          type: 'success'
        });
      })
    },
    handleAvatarSuccess2(file, fileList) {
      this.addForm.image_1 = file.data.image_name[0]
      this.imageUrl = URL.createObjectURL(fileList.raw);
    },
    handleRemove3(fileList) {
      this.addForm.image_2 = ''
      deleteImage({
        image_name: fileList.url.split('/')[fileList.url.split('/').length - 1]
      }).then((res) => {
        this.$message({
          message: '删除成功',
          type: 'success'
        });
      })
    },
    handleAvatarSuccess3(file, fileList) {
      this.addForm.image_2 = file.data.image_name[0]
      this.imageUrl = URL.createObjectURL(fileList.raw);
    },
    handleRemove4(fileList) {
      this.addForm.image_3 = ''
      deleteImage({
        image_name: fileList.url.split('/')[fileList.url.split('/').length - 1]
      }).then((res) => {
        this.$message({
          message: '删除成功',
          type: 'success'
        });
      })
    },
    handleAvatarSuccess4(file, fileList) {
      this.addForm.image_3 = file.data.image_name[0]
      this.imageUrl = URL.createObjectURL(fileList.raw);
    },
    handleRemove5(fileList) {
      this.addForm.image_4 = ''
      deleteImage({
        image_name: fileList.url.split('/')[fileList.url.split('/').length - 1]
      }).then((res) => {
        this.$message({
          message: '删除成功',
          type: 'success'
        });
      })
    },
    handleAvatarSuccess5(file, fileList) {
      this.addForm.image_4 = file.data.image_name[0]
      this.imageUrl = URL.createObjectURL(fileList.raw);
    },
    computerRate() {
      if (this.addForm.group_price && this.addForm.market_price) {
        this.addForm.rate = (((this.addForm.market_price - this.addForm.group_price) / this.addForm.market_price) * 100).toFixed(0) + '%'
      }
    },
    //新增
    addSubmit: function() {
      console.log(this.addForm)
      if (!this.addForm.image_0 || !this.addForm.image_1 || !this.addForm.image_2 || !this.addForm.image_3 || !this.addForm.image_4) {
        this.$message({
          message: '产品图片必须上传五张！',
          type: 'warning'
        });
        return false;
      }
      this.$refs.addForm.validate((valid) => {
        if (valid) {
          this.$confirm('确认提交吗？', '提示', {}).then(() => {
            this.addLoading = true;
            addProducts(this.addForm).then((res) => {
            console.log('res')
              this.addLoading = false;
              this.$message({
                message: '提交成功',
                type: 'success'
              });
              this.addFormVisible = false;
              this.getProducts();
            });
          });
        }
      });
    },
    //删除
    handleDel: function(index, row) {
      this.$confirm('确认删除该记录吗?', '提示', {
        type: 'warning'
      }).then(() => {
        this.listLoading = true;
        let para = {
          product_ids: row.product_id
        };
        removeProducts(para).then((res) => {
          this.listLoading = false;
          this.$message({
            message: '删除成功',
            type: 'success'
          });
          this.getProducts();
        });
      }).catch(() => {

      });
    },
    selsChange: function(sels) {
      this.sels = []
      sels.map((item) => {
        this.sels.push(item.product_id)
      })
    },
    //批量删除
    batchRemove: function() {
      this.$confirm('确认删除选中记录吗？', '提示', {
        type: 'warning'
      }).then(() => {
        this.listLoading = true;
        let para = {
          product_ids: this.sels.join(',')
        };
        removeProducts(para).then((res) => {
          this.listLoading = false;
          this.$message({
            message: '删除成功',
            type: 'success'
          });
          this.getProducts();
        });
      }).catch(() => {

      });
    },
    resetForm() {
      this.addForm = {
        name: '',
        community_id: [],
        brief: '',
        category_id: [],
        group_price: 0,
        rate: '',
        market_price: 0,
        charge_unit: '',
        group_number: 0,
        sell_point: '',
        detail: '',
        introduction: '',
        transport_sale: '',
        image_0: '',
        image_1: '',
        image_2: '',
        image_3: '',
        image_4: ''
      }
      this.image_0 = []
      this.image_1 = []
      this.image_2 = []
      this.image_3 = []
      this.image_4 = []
    },
    onEditorBlur(quill) {
      console.log('editor blur!', quill)
    },
    onEditorFocus(quill) {
      console.log('editor focus!', quill)
    },
    onEditorReady(quill) {
      console.log('editor ready!', quill)
    },
    onEditorChange({
      quill,
      html,
      text
    }) {
      console.log('editor change!', quill, html, text)
      this.content = html
    }
  },
  computed: {
    editor() {
      return this.$refs.myQuillEditor.quill
    }
  },
  components: {
    //使用编辑器
    quillEditor
  },
  mounted() {
    this.getProducts();
    this.computerRate()
  }
}
</script>

<style scoped>
.l-style {
  color: #333;
  margin-right: 5px;
}

.avatar-uploader-icon {
  font-size: 32px;
  color: #8c939d;
  width: 100px;
  height: 100px;
  line-height: 100px;
  text-align: center;
  border: 1px solid #c0ccda;
  border-radius: 6px;
  box-sizing: border-box;
}
.pictext {
    display: block;
    margin-top: -8px;
    font-size: 13px;
    color: #8c939d;
    width: 700px;
    height: 25px;
    margin-bottom: 15px;
    margin-left: 90px;
}
.avatar-uploader {
  display: inline-block;
}
</style>



